/*
	Autocomplete
*/

@mixin output {
    .handsontable {

        /* Arrow */
        .htAutocompleteArrow { 
            float: inline-end;
            cursor: default;
            width: 16px; 
            height: 16px; 
            font-size: 0;
            position: relative;
            top: calc( (var(--hot-base-line-height) - 16px ) * .5);
            margin-inline-end: calc(var(--hot-base-grid) - var(--hot-cell-horizontal-padding));

            &:after {
                width: 4px;
                height: 4px;
                content:'';
                display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: rotate(45deg) translate(-100%);
                border-right: 1.5px solid var(--hot-read-only-text);
                border-bottom: 1.5px solid var(--hot-read-only-text);
            }
        }
    }
}
